import service from "..";
//获取歌单详情页的数据
export function getMusicItemList(data){
    return service({
        mathod:'GET',
        url:`/playlist/detail?id=${data}`
    })
}

//获取歌单所有歌曲
export function getAllMusicList(data){
    return service({
        mathod:'GET',
        url:`/playlist/track/all?id=${data}&limit=20&offset=0`
    })
}

//获取音乐歌词
export function getMusicLyric(data){
    return service({
        mathod:'GET',
        url:`/lyric?id=${data}`
    })
}

