<template>
  <div class="musicList">
    <div class="musicTop">
      <div class="title">精选达人歌单</div>
      <div class="more">查看更多</div>
    </div>
    <div class="musicContent">
      <van-swipe
        :loop="false"
        :width="150"
        :show-indicators="false"
        class="my-swipe"
      >
        <van-swipe-item
          v-for="item in musicList"
          :key="item"
          style="margin: 6px"
        >
          <router-link :to="{path:'/musicdetails',query:{id:item.id}}">
            <img :src="item.picUrl" alt="" />
            <span class="playCount">
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#icon-bofang1"></use>
              </svg>
            </span>
            <span class="playCountNum">{{ changeCount(item.playCount) }}</span>
            <span class="name">{{ item.name }}</span>
          </router-link>
        </van-swipe-item>
      </van-swipe>
    </div>
  </div>
</template>

<script>
import { getMusicList } from "@/axios/api/home";
export default {
  data() {
    return {
      musicList: [],
    };
  },
  methods: {
    //获取音乐列表
    async getMusicList() {
      let res = await getMusicList();
      this.musicList = res.data.result;
    },
    //处理播放量数字
    changeCount(num) {
      if (num >= 100000000) {
        return (num / 10000000).toFixed(1) + "亿";
      } else if (num >= 10000) {
        return (num / 10000).toFixed(1) + "万";
      }
    },
  },
  mounted() {
    this.getMusicList();
  },
};
</script>

<style lang="less" scoped>
.musicList {
  width: 100%;
  height: 5rem;
  padding: 0.2rem;
  .musicTop {
    width: 100%;
    height: 0.5rem;
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.2rem;
    .title {
      font-size: 0.3rem;
      font-weight: 900;
    }
    .more {
      border: 1px solid #ccc;
      text-align: center;
      line-height: 0.5rem;
      padding: 0 0.2rem;
      font-size: 0.25rem;
      border-radius: 0.3rem;
    }
  }
  .musicContent {
    width: 100%;
    height: 4rem;
    .my-swipe {
      height: 100%;
      position: relative;
      img {
        width: 100%;
        height: 3rem;
        border-radius: 10px;
      }
      .playCount {
        position: absolute;
        right: 0.2rem;
        bottom: 1.2rem;
      }
      .playCountNum {
        position: absolute;
        color: #fff;
        left: 0.15rem;
        top: 0.1rem;
      }
    }
  }
}
</style>