<template>
  <div class="FooterMusic">
    <div class="footerLeft" @click="updateDetailShow">
      <img :src="playList[playListIndex].al.picUrl" alt="" />
      <div style="width: 60%">
        <p>
          {{
            playList[playListIndex].name
              ? playList[playListIndex].name
              : "Pink Champagne"
          }}
        </p>
        <span>横划切换上下首</span>
      </div>
    </div>
    <div class="footerRight">
      <svg
        class="icon"
        aria-hidden="true"
        @click="play"
        style="padding: 3px"
        v-if="isBtnShow"
      >
        <use xlink:href="#icon-bofang-copy"></use>
      </svg>
      <svg
        class="icon"
        aria-hidden="true"
        @click="play"
        style="padding: 3px"
        v-else
      >
        <use xlink:href="#icon-a-zanting1"></use>
      </svg>
      <svg class="icon" aria-hidden="true" style="padding: 3px">
        <use xlink:href="#icon-liebiao-copy"></use>
      </svg>
    </div>
    <audio
      ref="audio"
      :src="`https://music.163.com/song/media/outer/url?id=${playList[playListIndex].id}.mp3`"
    ></audio>
    <!-- 弹出层 -->
    <van-popup
      v-model:show="detailShow"
      position="bottom"
      :style="{ height: '100%', width: '100%' }"
    >
      <MusicDetail
        :musicList="playList[playListIndex]"
        :play="play"
        :isBtnShow="isBtnShow"
        :addDuration="addDuration"
      ></MusicDetail>
    </van-popup>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import MusicDetail from "@/components/Item/MusicDetail.vue";
export default {
  data() {
    return {
      Interval: 0,
    };
  },
  computed: {
    ...mapState(["playList", "playListIndex", "isBtnShow", "detailShow"]),
  },
  components: {
    MusicDetail,
  },
  methods: {
    play() {
      //判断音乐是否播放
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
        this.updateIsBtnShow(false);
        this.updateTime(); //播放就调用函数
      } else {
        this.$refs.audio.pause();
        this.updateIsBtnShow(true);
        clearInterval(this.Interval); //暂停就清除定时器
      }
    },
    addDuration() {
      this.updateDuration(this.$refs.audio.duration);
    },
    updateTime() {
      setInterval(() => {
        this.updateCurrentTime(this.$refs.audio.currentTime);
      }, 1000);
    },
    ...mapMutations([
      "updateIsBtnShow",
      "updateDetailShow",
      "updateCurrentTime",
      "updateDuration",
    ]),
  },
  mounted() {
    // console.log(this.$refs);
    this.$store.dispatch("getLyric", this.playList[this.playListIndex].id);
    this.updateTime();
  },
  updated() {
    this.$store.dispatch("getLyric", this.playList[this.playListIndex].id);
    this.addDuration();
  },
  watch: {
    playListIndex() {
      //切换自动播放音乐
      this.$refs.audio.autoplay = true;
      if (this.$refs.audio.paused) {
        this.updateIsBtnShow(false);
      }
    },
    playList() {
      if (this.isBtnShow) {
        this.$refs.audio.autoplay = true;
        this.updateIsBtnShow(false);
      }
    },
  },
};
</script>

<style lang="less">
.FooterMusic {
  width: 100%;
  height: 1.2rem;
  background-color: rgb(255, 255, 255);
  position: fixed;
  bottom: 0;
  border-top: 1px solid #ccc;
  display: flex;
  padding: 0.2rem;
  justify-content: space-between;
  .footerLeft {
    width: 60%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    p {
      font-size: 15px;
      //隐藏多余文字 只显示一行
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      word-break: break-all;
    }
    span {
      font-size: 10px;
      color: #00000071;
    }
    img {
      width: 0.9rem;
      height: 0.9rem;
      border-radius: 50%;
      border: 7px solid #000000;
    }
  }
  .footerRight {
    width: 20%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .icon {
      width: 0.55rem;
      height: 0.55rem;
    }
  }
}
</style>