<template>
  <div class="root">
    <div class="detailTop">
      <img :src="musicList.al.picUrl" alt="" />
      <div class="bgMusk"></div>
      <div class="leftDetail">
        <svg class="icon" aria-hidden="true" @click="backHome">
          <use xlink:href="#icon-a-xiajiantou1"></use>
        </svg>
      </div>
      <div class="midDetail">
        <div class="marquee">
          <!-- <marquee behavior="scroll" direction="right" scrolldelay="1000">{{
            musicList.name
          }}</marquee> -->
          <p>{{ musicList.name }}</p>
          <span v-for="(item, i) in musicList.ar" :key="i">
            {{ item.name ? item.name : "undefined" }}
          </span>
          <span>></span>
        </div>
      </div>
      <div class="rightDetail">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-a-fenxiang2"></use>
        </svg>
      </div>
    </div>
    <div class="detailContent" v-show="!isLyricShow">
      <img
        src="@/assets/磁针.png"
        alt=""
        class="cizhen"
        :class="{ cizhen_active: !isBtnShow }"
      />
      <img src="@/assets/磁盘.png" alt="" class="cipan" />
      <img
        :src="musicList.al.picUrl"
        alt=""
        class="cipanbg"
        @click="isLyricShow = 'true'"
        :class="{ cipanbg_active: !isBtnShow, cipanbg_paused: isBtnShow }"
      />
    </div>
    <div class="musicLyric" ref="musicLyric" v-show="isLyricShow">
      <p
        v-for="item in lyric"
        :key="item"
        :class="{
          active:
            currentTime * 1000 >= item.time && currentTime * 1000 < item.pre,
        }"
      >
        {{ item.lyc }}
      </p>
    </div>
    <div class="detailFooter">
      <div class="footerTop">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-aixin"></use>
        </svg>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-a-xiazai2"></use>
        </svg>
        <svg class="icon" aria-hidden="true" style="padding: 0">
          <use xlink:href="#icon-heijiaochangpian-copy"></use>
        </svg>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-a-pinglun2"></use>
        </svg>
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-a-gengduo1"></use>
        </svg>
      </div>
      <div class="footerLine">
        <input
          type="range"
          class="Line"
          min="0"
          :max="duration"
          v-model="currentTime"
          step="0.05"
        />
      </div>
      <div class="footerBottom">
        <svg
          class="icon"
          aria-hidden="true"
          style="width: 0.5rem; height: 0.5rem"
        >
          <use xlink:href="#icon-xunhuan"></use>
        </svg>
        <svg class="icon" aria-hidden="true" @click="goPlay(-1)">
          <use xlink:href="#icon-shangyishou"></use>
        </svg>
        <svg
          class="icon"
          aria-hidden="true"
          @click="play"
          v-if="isBtnShow"
          style="width: 1rem; height: 1rem"
        >
          <use xlink:href="#icon-a-bofang4"></use>
        </svg>
        <svg
          class="icon"
          aria-hidden="true"
          @click="play"
          v-else
          style="width: 1rem; height: 1rem"
        >
          <use xlink:href="#icon-a-zanting2"></use>
        </svg>
        <svg class="icon" aria-hidden="true" @click="goPlay(1)">
          <use xlink:href="#icon-xiayishou"></use>
        </svg>
        <svg
          class="icon"
          aria-hidden="true"
          style="width: 0.45rem; height: 0.45rem"
        >
          <use xlink:href="#icon-a-24gf-playlist"></use>
        </svg>
      </div>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
export default {
  data() {
    return {
      isLyricShow: false,
    };
  },
  computed: {
    ...mapState([
      "lyricList",
      "currentTime",
      "playListIndex",
      "playList",
      "duration",
    ]),
    //对音乐列表进行分割
    lyric: function () {
      let arr;
      if (this.lyricList.lyric) {
        arr = this.lyricList.lyric.split(/[(\r\n)\r\n]+/).map((item, i) => {
          let min = item.slice(1, 3);
          let sec = item.slice(4, 6);
          let mill = item.slice(7, 10); //切割毫秒
          let lyc = item.slice(11, item.length);
          let time =
            parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill);
          if (isNaN(Number(mill))) {
            mill = item.slice(7, 9);
            lyc = item.slice(10, item.length);
            time =
              parseInt(min) * 60 * 1000 + parseInt(sec) * 1000 + parseInt(mill);
          }
          return { min, sec, mill, lyc, time };
        });
        arr.forEach((item, i) => {
          if (i === arr.length - 1 || isNaN(arr[i + 1].time)) {
            item.pre = 100000;
          } else {
            item.pre = arr[i + 1].time;
          }
        });
      }
      // console.log(arr);
      return arr;
    },
  },
  mounted() {
    // console.log(this.musicList);
    // console.log(this.lyricList.lyric);
    this.addDuration();
  },
  methods: {
    ...mapMutations([
      "updateIsBtnShow",
      "updateDetailShow",
      "updatePlayListIndex",
    ]),
    backHome() {
      this.isLyricShow = false;
      this.updateDetailShow();
    },
    goPlay(num) {
      let index = this.playListIndex + num;
      if (index < 0) {
        index = this.playList.length - 1;
      } else if (index == this.playList.length) {
        index = 0;
      }
      this.updatePlayListIndex(index);
    },
  },
  watch: {
    currentTime(newvalue) {
      let p = document.querySelector("p.active");
      if (p) {
        if (p.offsetTop > 300) {
          this.$refs.musicLyric.scrollTop = p.offsetTop - 300;
        }
      }
      if (newvalue === this.duration) {
        if (this.playListIndex === this.playList.length - 1) {
          this.updatePlayListIndex(0);
          this.play()
        } else {
          this.updatePlayListIndex(this.playListIndex + 1);
        }
      }
    },
  },
  props: ["musicList", "isBtnShow", "play", "addDuration"],
};
</script>

<style lang="less">
.root {
  .detailTop {
    display: flex;
    justify-content: space-between;
    box-sizing: border-box;
    // background-blend-mode: darken;
    img {
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -2;
      filter: blur(40px);
    }
    .bgMusk {
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      background-color: #000000;
      opacity: 0.35;
    }
    .leftDetail {
      padding: 25px;
      .icon {
        width: 0.3rem;
      }
    }
    .midDetail {
      color: #fff;
      font-size: 17px;
      text-align: center;
      padding: 20px;
      font-weight: 500;
      span {
        color: #ffffffb4;
        font-size: 12px;
        font-weight: 200;
      }
    }
    .rightDetail {
      padding: 25px;
    }
  }
  .detailContent {
    width: 100%;
    height: 9rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    .cizhen {
      width: 2rem;
      height: 3rem;
      position: absolute;
      left: 46%;
      transform-origin: 0 0;
      transform: rotate(-18deg);
      transition: all 1s;
    }
    .cizhen_active {
      width: 2rem;
      height: 3rem;
      position: absolute;
      left: 46%;
      transform-origin: 0 0;
      transform: rotate(1deg);
      transition: all 1s;
    }
    .cipan {
      width: 6.3rem;
      height: 6.3rem;
      position: absolute;
      bottom: 0.9rem;
      z-index: -1;
    }
    .cipanbg {
      width: 4rem;
      height: 4rem;
      border-radius: 50%;
      position: absolute;
      bottom: 2.05rem;
      animation: cipanRotate 23s linear infinite;
    }
    .cipanbg_active {
      animation-play-state: running;
    }
    .cipanbg_paused {
      animation-play-state: paused;
    }
    @keyframes cipanRotate {
      0% {
        transform: rotateZ(0deg);
      }
      100% {
        transform: rotateZ(360deg);
      }
    }
  }
  .musicLyric {
    width: 100%;
    margin: 0 auto;
    padding: 0px 35px;
    font-size: 18px;
    height: 10.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 0.2rem;
    overflow: scroll;
    p {
      color: #e1e1e19d;
      margin-bottom: 0.6rem;
      font-size: 15px;
      text-align: center;
    }
    .active {
      color: #fff;
      font-size: 19px;
      transition: all 0.2s ease-in-out;
    }
  }
  .detailFooter {
    width: 100%;
    height: 3rem;
    position: absolute;
    bottom: 0.8rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .footerTop {
      width: 100%;
      height: 1rem;
      display: flex;
      justify-content: space-around;
      align-items: center;
      .icon {
        width: 0.7rem;
        height: 0.7rem;
        padding: 5px;
      }
    }
    .footerLine {
      padding: 0px 20px;
      input[type="range"] {
        // border-radius: 10px;
        // background: linear-gradient(to right, #059CFA, white 100%, white);
      }
      .Line {
        width: 100%;
        height: 0.04rem;
      }
    }
    .footerBottom {
      width: 100%;
      height: 1rem;
      display: flex;
      justify-content: space-around;
      align-items: center;
      .icon {
        padding: 1px;
        width: 0.6rem;
        height: 0.6rem;
      }
    }
  }
}
</style>